::after,
::before {
    box-sizing: border-box;
}

audio,
canvas,
embed,
iframe,
img,
object,
svg,
video {
    display: block;
    max-width: 100%;
    width: 100%;
}

body {
    margin: 0;
    -webkit-text-size-adjust: 100%;
    -webkit-tap-highlight-color: transparent;
    background-color: var(--color-f6);
    scroll-behavior: unset !important;
}

p {
    font: var(--font-16);
    font-weight: normal;
    font-family: var(--font-Roboto);
    color: var(--color-black);
    margin-bottom: 3rem;
}

p:last-of-type {
    margin-bottom: 0;
}

@media(max-width:1549px) {
    p {
        margin-bottom: 2rem;
    }
}

p:empty {
    display: none !important;
}

a {
    text-decoration: none;
    transition: all 0.5s ease;
    -webkit-transition: all 0.5s ease;
    -moz-transition: all 0.5s ease;
    -ms-transition: all 0.5s ease;
    -o-transition: all 0.5s ease;
}

figure {
    margin-bottom: 0;
}

.site_main {
    position: relative;
}


h1 {
    font: var(--font-60);
    color: var(--color-black)
}

h2 {
    font: var(--font-52);
    color: var(--color-03);
    font-weight: 550;
    letter-spacing: -0.84px;
}

h3 {
    font: var(--font-36);
    color: var(--color-black);
}

h4 {
    font: var(--font-24);
    color: var(--color-black);
}

h5 {
    font: var(--font-21);
    color: var(--color-black);
}

h6 {
    font: var(--font-18);
    color: var(--color-black);
}

::-webkit-scrollbar {
    width: 0.5rem;
}

::-webkit-scrollbar-thumb {
    background-color: var(--color-da);
    border-radius: 0.2rem;
}

::-webkit-scrollbar-track {
    background-color: var(--color-f6);
    border-radius: 0.2rem;
}

.overlap_btn {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    font-size: 0;
}

.read_btn {
    display: inline-block;
    border-radius: 50%;
}

.read_btn>img {
    width: 2.8rem;
}

.read_btn:hover {
    background-color: var(--color-white);
}

.sec_title>h5 {
    font: var(--font-21);
    font-weight: 600;
    color: var(--color-03);
    letter-spacing: 2.1px;
}

.sec_title>h2 {
    font-weight: 200;
    color: var(--color-21);
}

.notify_btn {
    position: absolute;
    bottom: 3rem;
    right: 4rem;
    z-index: 1;
}

.notify_btn>a {
    display: inline-flex;
    align-items: center;
    gap: 3rem;
    font: var(--font-21);
    font-weight: normal;
    letter-spacing: 2.1px;
    text-transform: uppercase;
    color: var(--color-white);
    padding: 1.2rem 2.4rem 1.2rem 1.9rem;
    background: var(--color-f8);
    background: rgba(248, 152, 35, 0.76);
    backdrop-filter: blur(2px);
    -webkit-backdrop-filter: blur(2px);
}

.notify_btn>a>img {
    width: 2.9rem;
}

.swiper_nav {
    display: flex;
    gap: 0.4rem;
}

:is(.swiper-button-prev, .swiper-button-next) {
    position: relative;
    margin-top: 0;
    width: 3.5rem;
    height: 3.5rem;
    right: auto;
    left: auto;
    font-size: var(--font-12);
    font-weight: bold;
    color: var(--color-55);
    background-color: transparent;
    border: 0.1rem solid var(--color-55);
    transition: all .5s ease;
}

:is(.swiper-button-prev, .swiper-button-next):hover {
    color: var(--color-white);
    background-color: var(--color-55);
}

.swiper-button-prev::after {
    content: '\F284';
    font-family: bootstrap-icons !important;
    font-size: inherit;
}

.swiper-button-next::after {
    content: '\F285';
    font-family: bootstrap-icons !important;
    font-size: inherit;
}

.scrollbar_wrap {
    position: relative;
    display: flex;
    align-self: center;
}

.scrollbar_wrap::before {
    width: 100%;
    height: 0.1rem;
    position: absolute;
    content: "";
    background: #BFC7C4;
    top: 0.1rem;
    z-index: 1;
}

.scrollbar_wrap .swiper-horizontal>.swiper-scrollbar,
.facilty_section .swiper-scrollbar.swiper-scrollbar-horizontal {
    position: static;
}

.scrollbar_wrap .swiper-scrollbar-drag {
    background: var(--color-bb);
    box-shadow: 0px 3px 6px #00000012;
}

.scrollbar_wrap .swiper-scrollbar.swiper-scrollbar-horizontal {
    left: 0;
    width: 100%;
    height: 0.4rem;
    background-color: transparent;
    bottom: 0;
    top: 0;
}

@media(max-width:767px) {
    :is(.swiper-button-prev, .swiper-button-next) {
        font-size: 1.4rem;
        font-weight: normal;
    }
}

@media(max-width:330px) {
    :is(.swiper-button-prev, .swiper-button-next) {
        width: 3.2rem;
        height: 3.2rem;
    }
}

.shine {
    position: relative;
    overflow: hidden;
}

.shine::before {
    background: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.3) 100%);
    content: "";
    display: block;
    height: 100%;
    left: -100%;
    position: absolute;
    top: 0;
    transform: skewX(-25deg);
    width: 50%;
    z-index: 2;
}

.shine:hover::before,
.shine:focus::before {
    -webkit-animation: shine 0.85s;
    animation: shine 0.85s;
}

@-webkit-keyframes shine {
    100% {
        left: 125%;
    }
}

@keyframes shine {
    100% {
        left: 125%;
    }
}


.full-width {
    max-width: 1920px;
    margin-inline: auto;
}

.full_touch {
    max-width: 2700px;
    margin-inline: auto;
}



a:focus-visible {
    outline: none;
}

.links {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    font-size: 0;
}

.custom-list {
    padding-left: 0;
    list-style: none;
    margin-bottom: 0;
}

.custom-list li {
    position: relative;
    font: var(--font-16);
    padding-left: 2rem;
    color: var(--color-21);
}

.custom-list li::before {
    content: '';
    position: absolute;
    width: 0.6rem;
    height: 0.6rem;
    background-color: var(--color-73);
    border-radius: 100%;
    left: 0;
    top: 0.8rem;
}

/* Accordion Area Start */
.accordion-button {
    position: relative;
    cursor: pointer;
    display: flex;
    gap: 1rem;
    justify-content: space-between;
    align-items: center;
    background-color: transparent;
    width: 100%;
    text-align: left;
    font: var(--font-30);
    color: var(--color-2b);
    padding: 0;
    padding-block: 3rem;
    border: 0;
    transition: all 0.5s ease;
    -webkit-transition: all 0.5s ease;
    -moz-transition: all 0.5s ease;
    -ms-transition: all 0.5s ease;
    -o-transition: all 0.5s ease;
}

.accordion-button::after {
    content: '';
    position: relative;
    display: block;
    width: 1rem;
    height: 1rem;
    /* background: url(../images/chevron-down.svg) no-repeat transparent; */
    background-position: center;
    background-size: cover;
    transition: all 0.5s ease;
    -webkit-transition: all 0.5s ease;
    -moz-transition: all 0.5s ease;
    -ms-transition: all 0.5s ease;
    -o-transition: all 0.5s ease;
}

.accordion-button.active::after {
    rotate: 180deg;
}

.accordion-button.active {
    background-color: var(--color-f6);
}

.accordion-item:nth-child(1)>.accordion-button {
    border-top: 0;
}

.accordion-collapse {
    display: none;
}

.accordion-item:not(:first-child) {
    border-top: 0.1rem solid rgb(112 112 112 / 30%);
}

.accordion-body {
    background-color: var(--color-f6);
    padding-bottom: 4.9rem;
}

