.table-responsive {
    width: 100%;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
}

/* Modal Css */
.modal {
    display: none;
    position: fixed;
    z-index: 1090;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgb(0 0 0 / 70%);
    backdrop-filter: blur(1px);
    align-items: center;
    justify-content: center;
    opacity: 0;
    visibility: hidden;
    transition: display 0.3 sease, opacity 0.3s ease, visibility 0.3s ease;
}

.modal.show {
    opacity: 1;
    visibility: visible;
    display: flex;
}

.inner_main:has(.modal.show) {
    z-index: 1090;
}

body:has(.modal.show) .innerpage_banner {
    z-index: 0;
}

body:has(.modal.show) {
    overflow: hidden;
}

.modal-content {
    background: var(--color-ff);
    max-width: 70rem;
    width: 100%;
    position: relative;
    animation: ModalfadeIn 0.3s ease-in-out;
    display: flex;
    flex-direction: column;
    max-height: calc(100% - 3rem* 2);
}

.closeModal {
    position: absolute;
    top: 0.5rem;
    right: 0.5rem;
    height: 3rem;
    width: 3rem;
    font-size: var(--font-21);
    line-height: normal;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--color-17);
    color: var(--color-ff);
    opacity: 0.8;
}

.closeModal:hover {
    opacity: 1;
}

.modal-title {
    padding: 2rem;
    border-bottom: 0.1rem solid rgb(37 51 111 / 10%);
    padding-right: 6rem;
}

.modal-body {
    position: relative;
    flex: 1 1 auto;
    padding: 2rem;
    overflow-y: auto;
}

@keyframes ModalfadeIn {
    from {
        opacity: 0;
        transform: scale(0.9);
    }

    to {
        opacity: 1;
        transform: scale(1);
    }
}


/* livewire pagination */
@media(min-width:992px) {

    /* Reveal Animations */
    .image,
    .image2 {
        opacity: 0;
    }

    .reveal-image,
    .reveal-image:after {
        position: absolute;
        animation-delay: 0s;
        animation-iteration-count: 1;
        animation-duration: 1600ms;
        animation-fill-mode: both;
        animation-timing-function: ease-in-out;
    }

    .reveal-image2,
    .reveal-image2:after {
        position: absolute;
        animation-delay: .1s;
        animation-iteration-count: 1;
        animation-duration: 1800ms;
        animation-fill-mode: both;
        animation-timing-function: ease-in-out;
    }

    .reveal-image {
        opacity: 1;
        position: relative;
        animation-name: reveal-image;
    }

    .reveal-image2 {
        opacity: 1;
        position: relative;
        animation-name: reveal-image2;
    }

    .reveal-image3 {
        opacity: 1;
        position: relative;
        animation-name: fadeInline1;
        animation-delay: .1s;
        animation-iteration-count: 1;
        animation-duration: 1800ms;
        animation-fill-mode: both;
        animation-timing-function: ease-in-out;
        transform-origin: top;
    }

    @keyframes reveal-image {
        from {
            clip-path: inset(0 100% 0 0);
        }

        to {
            clip-path: inset(0 0 0 0);
        }
    }

    @keyframes reveal-image2 {
        from {
            clip-path: inset(0 0% 100% 0);
        }

        to {
            clip-path: inset(0 0 0 0);
        }
    }
}

.image-container {
    position: relative;
    overflow: hidden;
    padding-bottom: calc(50% / (var(--ratio)));
}

.custom_bullets {
    margin: 0;
    padding: 0;
}

.custom_bullets>li {
    position: relative;
    list-style: none;
    font-size: var(--font-16);
    line-height: var(--line-height-24);
    color: var(--color-black);
    font-weight: normal;
    padding-left: 3.5rem;
    margin-bottom: 2.5rem;
}

.custom_bullets>li:last-child {
    margin-bottom: 0;
}

.custom_bullets>li::before {
    content: '';
    position: absolute;
    width: 0.5rem;
    height: 0.5rem;
    background-color: var(--color-a8);
    left: 0;
    top: 1rem;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
}

@media(max-width:1549px) {
    .custom_bullets>li {
        padding-left: 2rem;
    }

    .custom_bullets>li::before {
        width: 0.56rem;
        height: 0.56rem;
        top: 0.6rem;
    }
}

@media(max-width:767px) {
    .custom_bullets>li {
        padding-left: 2rem;
    }

    .custom_bullets>li::before {
        width: 0.46rem;
        height: 0.46rem;
        top: 0.6rem;
    }
}

/* Search Area */
.search-section {
    padding-block: 6rem 10rem;
    min-height: 60vh;
}

.search_bx {
    position: relative;
    background-color: var(--color-white);
    padding: 2rem 5rem 2rem 1.8rem;
    margin-bottom: 2rem;
}

.search_bx:last-child {
    margin-bottom: 0;
}

.search_bx>h5 {
    font-size: var(--font-24);
    font-weight: 600;
    color: var(--color-black);
    line-height: 1.16;
    margin-bottom: 1.5rem;
    transition: all .5s ease-out;
}

.search_bx>.link {
    font-size: var(--font-18);
    font-weight: 400;
    color: var(--color-black);
    line-height: 1.16;
    margin-bottom: 1.5rem;
    font-family: var(--font-intervogue);
}

.search_bx>p {
    font-size: var(--font-18);
    font-weight: 400;
    color: var(--color-black);
    line-height: 1.4;
    margin-bottom: 0;
    font-family: var(--font-intervogue);
}

.search_bx:has(a:hover)>h6 {
    color: var(--color-96);
}

.search_bx:has(a:hover)::after {
    background-position: left bottom;
    color: var(--color-white);
}

.search_bx:has(a:hover)::before,
.search_bx::before {
    background-color: var(--color-f0);
    position: absolute;
    bottom: 0;
    left: 0;
    content: '';
    height: 0.1rem;
    width: 60%;
    transition: all 0.5s ease;
}

.search_bx::after {
    content: '\F285';
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 2rem;
    font-family: bootstrap-icons !important;
    flex-shrink: 0;
    width: 2.6rem;
    height: 2.6rem;
    line-height: 1;
    font-size: var(--font-11);
    border: .1rem solid #D2D2D2;
    color: var(--color-white);
    background-color: transparent;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    font-weight: 800;
    background: linear-gradient(to right, var(--color-84) 49%, var(--color-96) 49%);
    background-size: 210% 110%;
    background-position: right bottom;
    transition: all .5s ease-out;
}

.streched_link {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    bottom: 0;
    z-index: 99;
    font-size: 0;
}